<template>
  <div>
      <!-- swiper -->
      <swiper :options="swiperOption">
        <swiper-slide><img src="https://robin.ml/vue-blog/static/images/1.jpg"></swiper-slide>
        <swiper-slide><img src="https://robin.ml/vue-blog/static/images/1.jpg"></swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
  </div>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper'
  export default {
    name: "Swiper",
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        swiperOption: {
          slidesPerView: 1,
          spaceBetween: 30,
          loop: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
    
        }
      }
    }
  }

</script>

<style scoped>
.swiper-slide {
     width: 100%;
  padding-top: 40%;
  position: relative;
}
.swiper-slide  img {
    width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}


</style>



